
body {
	background-color: #7DB800;
}
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #1A1A1A;
	margin-top: 60px;
	max-width: 50%;
	min-width: 512px;
	margin: auto;
	background-color: #ECECEC;
	border-radius: 4px;
	margin-top: 12px;
	margin-bottom: 24px;
	padding-bottom: 24px;
}
.textstuff {
	padding: 36px 64px 12px 64px;
}
img {
	max-width: 100%;
	border-radius: 4px 4px 0 0;
	display: inline-block;
	position: relative;
}
.main-title {
	font-size: 1.5em;
	background-color: #005E5A;
	color: #ECECEC;
	padding: 12px 24px;
}
.main-butt,
.button-back,
.button-forward {
	border: 0px 2px 2px 0px solid;
	background: #FD0006;
	border-color: #FC999B;
	color: #ECECEC;
	cursor: pointer;
	margin-top: 0;
}
.main-butt {
	display: block;
	margin: auto;
	margin-bottom: 36px;
	padding: 24px;
	width: 352px;
	max-width: 90%;
	border-radius: 6px;
	font-size: 1.6em;
	transition-duration: 0.3s;
}
.button-back:hover,
.main-butt:hover,
.button-forward:focus {
	background-color: #FD393E;
	color: #1A1A1A;
}
.button-back:focus,
.main-butt:focus,
.button-forward:focus {
	outline: 0;
}
.button-back,
.button-forward {
	position: fixed;
	font-size: 3em;
	width: 72px;
	height: 72px;
	border-radius: 100%;
	transition-duration: 0.3s;
}
.button-back {
	left: 12vw;
	top: 12vh;
}
.button-forward {
	right: 12vw;
	bottom: 12vh;
}
p {
	margin: auto;
	text-align: left;
	font-size: 1.15em;
	line-height: 1.5em;
}



